<template>
  <tr>
      <td @click="otvoriDialog">{{sala.id}}</td>
      <td @click="otvoriDialog">{{sala.naziv}}</td>
      <td><v-btn @click="dialog = true">Kalendar</v-btn></td>
      <v-dialog
                v-model="dialog"
                max-width="600"
                >
                <v-card>
                    <Kalendar v-bind:events="termini"/>
                </v-card>
                
      </v-dialog>
      <td><v-btn icon @click="deleteSala"><v-icon>mdi-delete</v-icon></v-btn></td>
  </tr>
  
</template>

<script>
import axios from "axios";
import Kalendar from "./Kalendar.vue";
export default {
    data: () => ({
        dialog : false
    }),
    props: ["sala","termini"],
    methods: {
      deleteSala: function(){
        axios
            .delete('sala/'+this.sala.id)
            .then(() => {
                this.$store.commit("setSnackbar", {text:"UspeÅ¡no ste obrisali salu", color: "success"});
                this.$emit("del-sala",this.sala.id);
            })
            .catch(() => { this.$store.commit("setSnackbar", {text:"Sala se veÄ koristi u pregledu", color: "error"});

            });
      },
      otvoriDialog: function(){
          this.$emit("otvori",this.sala.id);
      }
    },
    components: {
        Kalendar
    }
}
</script>

